export default {
	props: {
		// 
		title: {
			type: [String, Number],
			default: ''
		},
		// 
		label: {
			type: [String, Number],
			default: ''
		},
		// 
		value: {
			type: [String, Number],
			default: ''
		},
		// ()
		icon: {
			type: String,
			default: ''
		},
		// cell
		disabled: {
			type: Boolean,
			default: false
		},
		// 
		border: {
			type: Boolean,
			default: true
		},
		// (value)
		center: {
			type: Boolean,
			default: true
		},
		// URL
		url: {
			type: String,
			default: ''
		},
		// uvuiroute
		linkType: {
			type: String,
			default: 'navigateTo'
		},
		// ()
		clickable: {
			type: Boolean,
			default: false
		},
		// 
		isLink: {
			type: Boolean,
			default: false
		},
		// (input)
		required: {
			type: Boolean,
			default: false
		},
		// 
		rightIcon: {
			type: String,
			default: 'arrow-right'
		},
		// leftupdown
		arrowDirection: {
			type: String,
			default: ''
		},
		// 
		iconStyle: {
			type: [Object, String],
			default: () => {
				return {}
			}
		},
		// 
		rightIconStyle: {
			type: [Object, String],
			default: () => {
				return {}
			}
		},
		// 
		titleStyle: {
			type: [Object, String],
			default: () => {
				return {}
			}
		},
		// large
		size: {
			type: String,
			default: ''
		},
		// cell
		stop: {
			type: Boolean,
			default: true
		},
		// cell
		name: {
			type: [Number, String],
			default: ''
		},
		// 
		cellStyle: {
			type: [Object, String],
			default: () => {}
		},
		...uni.$uv?.props?.cell
	}
}