export default {
	props: {
		// 
		title: {
			type: String,
			default: ''
		},
		// 
		value: {
			type: String,
			default: ''
		},
		// 
		label: {
			type: String,
			default: ''
		},
		// 
		disabled: {
			type: Boolean,
			default: false
		},
		// 
		isLink: {
			type: Boolean,
			default: true
		},
		// 
		clickable: {
			type: Boolean,
			default: true
		},
		// 
		border: {
			type: Boolean,
			default: true
		},
		// 
		align: {
			type: String,
			default: 'left'
		},
		// 
		name: {
			type: [String, Number],
			default: ''
		},
		// 
		icon: {
			type: String,
			default: ''
		},
		// ms
		duration: {
			type: Number,
			default: 300
		},
		...uni.$uv?.props?.collapseItem
	}
}