
class myIp{
  
  getIp(){
    let account = uni.getAccountInfoSync()
    console.log('', account.miniProgram.envVersion);
    let devHttp = 'http://192.168.243.211:33088/' // 
    // let devHttp = 'https://yirc99.cn/api/' // 
    let trialHttp = 'https://yirc99.cn/api/' // 
    let releaseHttp = 'https://yirc99.cn/api/' // 
    let http;
    // 
    if (account.miniProgram.envVersion == 'develop') {
      http = devHttp
    } else if (account.miniProgram.envVersion == 'release') {
      http = releaseHttp
    } else {
      http = trialHttp
    }
    return http
  }
  
}

export default new myIp()