import CryptoJS from 'crypto-js';
import MyIp from '@/common/myIp.js'

class aes {
  
  
   async reloadAESKey(){
    //AES  AESkey
    let res = await uni.request({
      url: this.http + 'common/AESKey',
      method: 'POST'
    })
    if(res.data.code == 200){
      console.log('---------------------------',res.data.data);
      this.aesKey = res.data.data
    }
    console.log('AESkey',res.data);
  }

  constructor() {
    // this.aesKey = 'Written by YIRC99 Thank you for liking this software';
    this.aesKey = 'sZi8MyGoD0';
    this.http = MyIp.getIp()
  }
  

  async decrypt(encryptedData) {
    //  IV 
    const parts = encryptedData.split('|');
    const iv = CryptoJS.enc.Base64.parse(parts[0]); // ivbase64 base64

    const keyBytes = CryptoJS.enc.Utf8.parse(this.aesKey); //utf8 utf8

    // 
    const decryptedData = CryptoJS.AES.decrypt({
        ciphertext: CryptoJS.enc.Base64.parse(parts[1]) //base64
      },
      keyBytes, {
        iv: iv,
        mode: CryptoJS.mode.CBC,
        padding: CryptoJS.pad.Pkcs7
      }
    );
    
    let decryptedText
    try{
      decryptedText = decryptedData.toString(CryptoJS.enc.Utf8);
    } catch(e) {
      // 
      // console.log(' key',this.aesKey, e.message);
      await this.reloadAESKey()
      return null
    } 
    // console.log(' key',this.aesKey);
    // 
    let result = JSON.parse(decryptedText)
    return result
  }

}


export default new aes()